@import '../base';

.c-cart-lines {
  position: relative;

  .o-shipping-progress__title {
    padding-bottom: 1.0625rem;
  }

  &__bundle{
    &-items{
      margin: 0 0 t-to-rem(16) t-to-rem(10);
      line-height: 1.7;
    }
  }

  &__line {
    + #{&} {
      border-top: 1px solid $s-color--light-blue;
      padding-top: 3.625rem;
    }
    
    &-inner {
      position: relative;
    }
    

    &-title {
      display: block;
      color: $s-color--blue-green;
      background: $s-color--light-blue;
      padding: 1.9375rem 2.6875rem;

      &-text { 
        display: block;
        width: 80%;
        width: calc(100% - 5rem);
        cursor: pointer;
      }
    }

    &-image {
      display: block;
      position: absolute;
      right: .5rem;
      top: -1.5rem;
      width: 5rem;
    }

    &-body {
      padding: 1.4375rem 2.3125rem 2.3375rem;

      &-numbers {
        padding-top: 1.4375rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }


      &-remove {
        width: 1.25rem;
        height: 1.25rem;
        padding: 0.25rem;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        margin-left: auto;

        &,* { stroke: $s-color--blue-green; }
      }
    }
    

    &-price {
      @include t-font--type-7;
      font-size: t-to-rem(16);
      margin-left: auto;
      text-align: right;
      &.is-sample{
        margin-left: 0;

      }

      &-price{
        &.has-compare{
          margin-left: t-to-rem(8);
        }
      }

      &-compare{
        text-decoration: line-through;
        color: rgba($s-color--font-1, .5);
      }
    }

    &-treats {
      &-title {
        display: block;
        padding-bottom: 0.5rem;
      }

      &-text {
        color: $s-color--grey-green;
        padding-right: 5rem;
        padding-bottom: 1rem;
      }
    }
  }

  &__empty {
    text-align: center;
    margin: auto;
    padding: 4rem 1rem 8rem;

    &-title {
      padding-bottom: 1.5rem
    }
  }

  &__footer {
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: t-to-rem(2) solid rgba($s-color--background-secondary, 0.1);

    position: sticky;
    background: $s-color--white;


    text-align: center;


    &-inner {
      margin: auto;
      max-width: 107.5rem;
      padding: t-to-rem(36) 2.5rem;
    }

    &-total { display: none; }

    &-buttons {
      display: flex;
      flex-direction: column;

      &-checkout { 
        margin-top: 0.625rem; 

        .has-invalid-quantity &{
  
          &.o-btn{
            pointer-events: none;
            background: $s-color--disabled-grey;
          }
        }
        
      }
    }
  }

  @include t-media-query($s-xsmall-up) {
    &__line-body-remove {
      margin-left: 6.625rem;
    }
  }

  @include t-media-query($s-small-up) {
    &__footer {
      &-inner{
        padding: t-to-rem(38) 4.375rem;
      }

      &-total {
        display: block;
        padding-bottom: 1.5rem;
      }

      &-buttons {
        flex-direction: row;
        justify-content: space-between;

        &-checkout,&-continue {
          margin: 0;
          width: 45%;
          width: calc(50% - 0.625rem);
        }
      }
    }

    &__list-inner {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;
      // margin-bottom: 2.8125rem;

      &:after {
        content: '';
        display: block;
        position: absolute;
        height: 100%;
        left: 50%;
        top: 0;
        border-right: 1px solid $s-color--light-blue;
      }
    }

    &__line {
      width: 45%;
      width: calc(50% - 1.5rem);
      
      + #{&} {
        border: none;
        padding-top: 0;
      }

      &-body-remove {
        margin-left: auto;
      }
    }
  }

  @include t-media-query($s-medium-up) {

    &__list-inner{
      padding: 0 7%;
      max-width: t-to-rem(2000);
    }

    &__line {
      &-image {
        right: 0rem;
        width: 7rem;
      }

      &-inner {
        padding-top: 3.625rem;
        padding-bottom: 4.375rem;
      }
      

      &-treats-text {
        padding-right: 0;
      }

      &-body {
        width: 82%;

        &-numbers { padding-top: 2.1875rem; }
      }
      
    }

    &__footer{
      &-inner{
        padding: t-to-rem(76) 7%;
      }
      &-total { padding-bottom: 3rem; }
    }
  }

  @include t-media-query($s-large-up) {
    &__list-inner {
      max-width: 107.5rem;
      margin: auto;
    }

    &__line {
      &-title {
        max-width: calc(100% - 6rem);
        padding: 5.0625rem 5.8125rem 5.0625rem 6rem;
        &-text { max-width: none; }
      }

      &-image {
        width: 12rem;
      }

      &-body {
        padding: 2rem 5.6875rem;
      }
    }

  }
}